@import "variables";

@media screen and(max-width: 650px), screen and (max-device-width: 650px){
  body{
    .authorization{
      width: 100%;
      .author,
      .regist{
        form{
          p{
            .user,
            .shield,
            .phone,
            .envelope{
              left: 55px;
            }
            .asterisk{
              right: 55px;
            }
          }
        }
      }
      .close{
        display: block;
      }
    }
  }
}